import React from 'react';
import {Link, NavLink} from "react-router-dom";

const Menu = () => {
    return (
        <ul className={'flex items-center'}>
            <li className={'menu__item group'}>
                <NavLink className={'menu__item_link'} to={'/'}>Статистика</NavLink>
            </li>
            <li className={'menu__item group'}>
                <NavLink className={'menu__item_link'} to={'/dispensaries'}>Амбулатории</NavLink>
                <ul className="absolute z-5 bg-white  rounded-b-xl invisible group-hover:visible  -left-6 -right-6 top-2/3 py-3">
                    <li className={'mb-1'}>
                        <NavLink className={'px-3 py-1 text-black block transition hover:text-primary'} to={'/dispensaries/outpatient-reports'}>Расчеты</NavLink>
                    </li>
                    <li className={'mb-1'}>
                        <NavLink className={'px-3 py-1 text-black block transition hover:text-primary'} to={'/dispensaries/organizations'}>Организации</NavLink>
                    </li>
                    <li className={'mb-1'}>
                        <Link className={'px-3 py-1 text-black block transition hover:text-primary'} to={'#'}>Мед.работники</Link>
                    </li>
                </ul>
            </li>
            <li className={'menu__item group'}>
                <NavLink className={'menu__item_link'} to={'/hospitals'}>Стационары</NavLink>
                <ul className="absolute z-5 bg-white  rounded-b-xl invisible group-hover:visible  -left-6 -right-6 top-2/3 py-3">
                    <li className={'mb-1'}>
                        <NavLink className={'px-3 py-1 text-black block transition hover:text-primary'} to={'/hospitals/inpatient-reports'}>Расчеты</NavLink>
                    </li>
                    <li className={'mb-1'}>
                        <NavLink className={'px-3 py-1 text-black block transition hover:text-primary'} to={'/hospitals/organizations'}>Организации</NavLink>
                    </li>
                    <li className={'mb-1'}>
                        <Link className={'px-3 py-1 text-black block transition hover:text-primary'} to={'#'}>Прол.случаи </Link>
                    </li>
                </ul>
            </li>
            <li className={'menu__item'}>
                <NavLink className={'menu__item_link'} to={'/references'}>Справочники</NavLink>
            </li>
            <li className={'menu__item'}>
                <NavLink className={'menu__item_link'} to={'/settings'}>Администрирование</NavLink>
            </li>
        </ul>
    );
};

export default Menu;